import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import ContactService from '../../service/Contact_Service'
import Navbar from '../navbar/Navbar'
import './view_contact.css'

const View_Contact = () => {

    let { contactId } = useParams()
    let [contact, setContact] = useState({
        img: '',
        name: '',
        email: '',
        age: 0,
        address: '',
        company: '',
        contactNo: 0
    })

    let getContact = async () => {
        try {
            let response = await ContactService.getContact(contactId)
            setContact(response.data)
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        getContact()

        return (() => {
            setContact({
                ...contact
            })
        })
    }, [])

    return (
        <div>
            <Navbar />
            <div className="container mt-3">
                <h3 className='fw-bold'>Contact details</h3>
                <p className='fst-italic d-none d-sm-block'>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Inventore impedit ipsam numquam repellendus officia sed. Fuga, incidunt sapiente maiores corporis odit aspernatur corrupti eaque excepturi quae quasi atque delectus impedit!</p>
            </div>

            <div className="container mt-3">
                <div className="row align-items-center text-center">
                    <div className="col-lg-6 col-xl-4">
                        <img src={/^(http(s):\/\/.)[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)$/.test(contact.img) ? contact.img : "data:image/jpeg;base64,"}
                            className="rounded-circle view-img"
                        />
                    </div>
                    <div className="col-lg-6 text-start">
                        <ul className='list-group mt-4 mt-lg-0'>
                            <li className="list-group-item">
                                Name: <span className='fw-bold fst-italic'>{contact.name}</span>
                            </li>
                            <li className="list-group-item">
                                Email: <span className='fw-bold fst-italic'>{contact.email}</span>
                            </li>
                            <li className="list-group-item">
                                Age: <span className='fw-bold fst-italic'>{contact.age}</span>
                            </li>
                            <li className="list-group-item">
                                Address: <span className='fw-bold fst-italic'>{contact.address}</span>
                            </li>
                            <li className="list-group-item">
                                Company: <span className='fw-bold fst-italic'>{contact.company}</span>
                            </li>
                            <li className="list-group-item">
                                Contact No: <span className='fw-bold fst-italic'>{contact.contactNo}</span>
                            </li>
                        </ul>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12 col-md-4 col-lg-2 col-xl-2 d-grid mt-lg-5">
                        <Link to="/" className="btn btn-dark my-3">
                            <i className="fa fa-caret-left"></i> Back
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default View_Contact